.menu-toggle.active span {
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: all ease 0.1s;
  -webkit-transition: all ease 0.1s;
  -moz-transition: all ease 0.1s;
  -ms-transition: all ease 0.1s;
  -o-transition: all ease 0.1s;
