/*==================== Radio button styles begin ====================*/
.radio-container {
  position: relative;
  padding-left: 28px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.radio-container input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.radio-checkmark {
  position: absolute;
  display: block;
  width: 22px;
  height: 22px;
  background: #d7e0ec;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  content: "";
  left: 0px;
  top: 3px;
  bottom: 0;
  margin: auto;
  transition: all ease 0.3s;
  -webkit-transition: all ease 0.3s;
